<template>
  <div id="gameScreen" :gameCode="gameCode" :style="{'width': width + 'px'}">
    <ul v-for="(row, rindex) in screen" :key="rindex" :style="{'max-width': 100 / screen.length + '%'}">
      <template v-for="(col, cindex) in row">
        <li v-if="Number(col)" :key="cindex" :style="{'width': px + 'px'}"><img :src="require(`@img/games/${gameCode}/${col}.png`)" alt=""></li>
      </template>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'DefaultComponent',
  props: {
    gameCode: {
      type: String,
      default: '',
    },
    screen: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      px: 70,
    }
  },
  computed: {
    width() {
      let width = 0;
      width = this.screen.length;
      return width * this.px + 4 || 0;
    }
  },
}
</script>