<template>
  <div>
    <resultScreen :game-code="gameCode" :screen="free.detail[freeGamesIndex].symbol_result" :outSideEnable="free.detail[freeGamesIndex].stageEnable" />
    <div class="pager" v-if="free.detail.length > 1">
      <ul>
        <li class="prevPage" @click="chgPageFree(free.detail.length,-1)"><img src="data:image/svg+xml;base64," alt=""></li>
        <li><select v-model="freeGamesIndex"><option v-for="(item, index) in free.detail" :key="index" :value="index">{{ index+1 }}</option></select></li>
        <li>/</li>
        <li>{{ free.detail.length }}</li>
        <li class="nextPage" @click="chgPageFree(free.detail.length,1)"><img src="data:image/svg+xml;base64," alt=""></li>
      </ul>
    </div>
  </div>
</template>
<script>
import resultScreen from '@/components/pages/Home/resultScreen/index'
export default {
  name: 'Default',
  components: {
    resultScreen,
  },
  props: {
    free: {
      type: Object,
      default: () => {},
    },
    gameCode:{
      type: String,
      default: '',
    },
    parentIndex: {
      type: Number,
      default: 0,
    },
  },
  watch: {
    freeGamesIndex(next) {
      this.$emit('chgFreeGameIndex', this.parentIndex, next)
    },
  },
  data() {
    return {
      freeGamesIndex: 0,
    }
  },
  methods: {
    chgPageFree(pageCount, val) {
      if(this.freeGamesIndex + val < 0) {
        this.freeGamesIndex = pageCount - 1
      } else if (this.freeGamesIndex + val === pageCount) {
        this.freeGamesIndex = 0
      } else {
        this.freeGamesIndex +=  val
      }
      this.$emit('chgFreeGameIndex', this.parentIndex, this.freeGamesIndex)
    },
  },
}
</script>