<template>
  <div id="result" ref="result" :status="viewStatus" :data-lang="this.$i18n.locale" :class="{ 'pageLock': detailPage }">
    <template v-if="viewStatus === 'full'">
      <div class="result-header" ref="header">
        <div class="closeBtn" @click="closeHistory()"></div>
        <div class="title"><span>{{$t('BetHistory')}}</span></div>
        <section class="date-select">
          <span class="filter-lebel" :span-CustomizedSearch="this.$i18n.locale">{{$t('CustomizedSearch')}}</span>
          <div class="filter-input">
            <el-date-picker
              v-model="searchDate[0]"
              type="date"
              :placeholder="$t('SelectStartDate')"
              align="center"
              :editable="false"
              :clearable="false"
              :picker-options="pickerOptions">
            </el-date-picker>
            <span class="to-lebel">{{$t('To')}}</span>
            <el-date-picker
              v-model="searchDate[1]"
              type="date"
              :placeholder="$t('SelectEndDate')"
              align="center"
              :editable="false"
              :clearable="false"
              :picker-options="pickerOptions">
            </el-date-picker>
            <button class="el-button--info" @click="searchListByDate">{{$t('Search')}}</button>
          </div>
        </section>
        <section class="number-select">
          <span class="filter-lebel" :span-IDSearch="this.$i18n.locale">{{$t('IDSearch')}}</span>
          <div class="filter-input">
            <input type="text" v-model="itemNumber">
            <button class="el-button--info" @click="getListByID">{{$t('Search')}}</button>
          </div>
        </section>
      </div>
      <div class="result-content">
        <div class="prompt"><img src="@img/icon/info.svg" alt=""><span>{{$t('BetPrompt')}}</span></div>
        <div class="dataList">
          <ul class="_head">
            <li>{{ $t('RoundID') }}</li>
            <li>{{ $t('DateTime') }}</li>
            <li>{{ $t('BetTotal') }}</li>
            <li>{{ $t('TotalWin') }}</li>
          </ul>
          <perfect-scrollbar>
            <ol class="_list">
              <li v-if="dataList.length < 1" class="nodata">{{ $t('nodata') }}</li>
              <li v-else v-for="row in dataList" :key="row.round_code" @click="getItemDetail(row.round_code)" class="item">
                <div :data-label="$t('RoundID')"><a class="list-detail-item">{{ row.round_code }}</a></div>
                <div :data-label="$t('DateTime')"><span>{{ row.bet_time }}</span></div>
                <div :data-label="$t('BetTotal')"><span>{{ row.bet }}</span></div>
                <div :data-label="$t('TotalWin')"><span>{{ row.win }}</span></div>
              </li>
            </ol>
          </perfect-scrollbar>
        </div>
      </div>
      <div class="result-footer" ref="footer" v-show="Math.ceil(listTotal) >= 1">
        <button class="el-button--info" :disabled="currPage===1" @click="prevPage()" v-if="Math.ceil(listTotal) > 1"></button>
        <select v-model="currPage">
          <option v-for="(item,index) in Array(Math.ceil(listTotal / intPerPage))" :value="index+1" :key="index">{{ index+1 }}</option>
        </select>
        <i>/</i>
        <span>{{ Math.ceil(listTotal / intPerPage) }}</span>
        <button class="el-button--info" :disabled="currPage===Math.ceil(listTotal / intPerPage)" @click="nextPage()" v-if="Math.ceil(listTotal) > 1"></button>
      </div>
    </template>
    <div v-if="detailPage || (viewStatus === 'simple' && simpleReady)" class="detail-area" :game-id="gameCode">
      <component 
        :is="detailComponent"
        :game-token="gameToken"
        :game-code="gameCode"
        :round-id="roundID"
        :account="account"
        :api-url="apiUrl"
        :view-status="viewStatus"
        :detail-from-backstage="detailFromBackstage"
        :currency="currency"
        :game-list="gameList"
        :loading="loading"
        @detailPageSwitch="detailPageSwitch"
        @changeCurrency="changeCurrency"
        @changeLoading="changeLoading">
      </component>
    </div>
    <div v-if="loading" class="loader"></div>
  </div>
</template>
<script>
import axios from 'axios'
import moment from 'moment'
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import 'vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css'
import slotDetail from '@/components/pages/Home/slotDetail/index'
import fishDetail from '@/components/pages/Home/fishDetail/index'
import {BACKEND_LOCATION} from '@/constants/Endpoints'
import _ from 'lodash'
export default {
  name: 'Home',
  components: {
    PerfectScrollbar,
    slotDetail,
    fishDetail,
  },
  data(){
    return {
      viewStatus: 'full',
      simpleReady: false,
      searchDate: ['',''],
      dataList: [],
      dataAllList: [],
      currPage: 1,
      listTotal: 0,
      intPerPage: 10,
      itemNumber: '',
      account: '',
      defaultSize: [1280,720],
      maxDate: moment().format('YYYY-MM-DD'),
      minDate: moment().add(-6, 'days').format('YYYY-MM-DD'),
      pickerOptions: {
        disabledDate: (time) => {
          const eachTime = moment(time)
          return eachTime.isBefore(moment(this.minDate)) || eachTime.isAfter(moment(this.maxDate)) ? true : false
        }
      },
      searchStart: '',
      searchEnd: '',
      searchType: 1,
      gameToken: '',
      gameCode: '',
      oldGameList: [],
      detailPage: false,
      detailComponent: '', //newDetail : oldDetail
      apiUrl: '',
      roundID: '',
      detailFromBackstage: {}, /** 資料由後台傳入 */
      currency: '-',
      gameList: [],
      customDetail: [],
      gameFrame: false, // false: horizontal, true: vertical
      loading: false,
    }
  },
  computed: {},
  methods: {
    changeLoading(status){
      this.loading = status
    },
    prevPage() {
      if(this.currPage === 1) { return }
      this.currPage -=1
      this.searchType === 1 ? this.getListByDate() : this.getListByID()
    },
    nextPage() {
      if(this.currPage === Math.ceil(this.listTotal / this.intPerPage)) { return }
      this.currPage +=1
      this.searchType === 1 ? this.getListByDate() : this.getListByID()
    },
    /** 顯示/關閉細單 */
    detailPageSwitch () {
      this.detailPage = !this.detailPage
    },
    /** 查詢細單 */
    getItemDetail(roundID) {
      this.roundID = roundID
      this.detailPageSwitch()
    },
    /** 單號查找 */
    getListByID: _.debounce(function(){
      this.loading = true
      let api = `${this.apiUrl}/slot/betsummarys`
      const { gameToken, itemNumber, currPage, intPerPage } = this
      const params = {
        token : gameToken,
        round_code: itemNumber,
        query_type: 0,
        rows: intPerPage,
        page: currPage,
      }
      axios.post(api,params)
        .then(({data})=>{
            if (data.spin_summarys === null){
                this.listTotal = 0
                this.dataList = []
                this.currPage = 1
                this.account = ''
            }else{
            this.dataList = this.dataFormat(_.cloneDeep(data.spin_summarys), data.money_fraction_multiple)
            this.listTotal = data.total_count
            this.currPage = currPage
            this.account = data.user_name
            }
          this.gameFrame = data.game_frame
          this.detailComponent = this.detailMode(data.game_class_id)
        })
        .catch(function (error) {
          console.log(error);
        }).finally(() => {
          this.loading = false
        });
    },300),
    /** 日期區間搜尋 */
    searchListByDate() {
      if(!this.searchDate[0] || !this.searchDate[1]) { return false }
      if (moment(this.searchDate[0]).isAfter(moment(this.searchDate[1]))) {
        const _min = this.searchDate[1]
        const _max = this.searchDate[0]
        this.searchDate = [_min, _max]
      }

      this.currPage = 1
      this.getListByDate(moment(this.searchDate[0]).format('YYYY-MM-DD'), moment(this.searchDate[1]).format('YYYY-MM-DD'))
    },
    /** 日期查找 */
    getListByDate: _.debounce(function(start, end) {
      this.loading = true
      let api = `${this.apiUrl}/slot/betsummarys`
      const { gameToken, currPage, searchStart, searchEnd, intPerPage } = this

      const startDate = start ? moment(`${start} 00:00:00`).utc().valueOf() / 1000 : searchStart ?  searchStart : moment(`${moment().add(-6,'d').format('YYYY-MM-DD')} 00:00:00`).utc().valueOf() / 1000
      const endDate = end ? moment(`${end} 23:59:59`).utc().valueOf() / 1000 : searchEnd ?  searchEnd : moment(`${moment().format('YYYY-MM-DD')} 23:59:59`).utc().valueOf() / 1000
      this.searchStart = startDate
      this.searchEnd = endDate

      const params = {
        token: gameToken,
        query_type: 1,
        begin_time: this.searchStart,
        end_time: this.searchEnd,
        rows: intPerPage,
        page: currPage,
      }
      axios.post(api, params)
        .then(({data})=>{
          if(String(data.code) === '0') {
            if (data.spin_summarys === null){
                this.listTotal = 0
                this.dataList = []
                this.currPage = 1
                this.account = ''
            }else{
            this.dataList = this.dataFormat(_.cloneDeep(data.spin_summarys), data.money_fraction_multiple)
            this.listTotal = data.total_count
            this.currPage = currPage
            this.account = data.user_name
            }
            this.gameFrame = data.game_frame
            this.detailComponent = this.detailMode(data.game_class_id)
          }
        })
        .catch(function (error) {
          console.log(error);
        }).finally(() => {
          this.loading = false
        });
    },300),
    gameInit() {
      const gameInfo = parent.Entry
      if (gameInfo) {
        const { gameToken, gameCode } = gameInfo.getDetail
        if(gameToken) {
          this.gameToken = gameToken
        }
        if (gameCode) {
          this.gameCode = gameCode
        }
      }
      this.getListByDate()
    },
    closeHistory() {
      try{
        // parent.detailPageHide()
        parent.EventHandler.dispatch(parent.eEventName.closeWindow)
      }catch(e){
        // error 
      }
    },
    dataFormat(ary, multiple) {
      const data = ary
      data.map(v=>{
        v.bet_time = moment((v.bet_time)*1000).utcOffset(-240).format()
        v.bet = v.bet / multiple
        v.win = v.win / multiple
      })
      return data
    },
    /** 後台查細單 */
    postGetInfo(e) {
      if (typeof e.data === 'string') {
        const data = JSON.parse(e.data)
        this.backstageDetailInit(data)
      }
    },
    /** 後台查細單初始化 */
    backstageDetailInit(data) {
      this.gameCode = data.gameCode
      this.detailFromBackstage = data.gameDetail
      this.roundID = data.round_id
      this.account = this.detailFromBackstage.user_name
      switch(data.language) {
        case 'vi':
          this.$i18n.locale = 'vi' 
          break
        case 'ko':
          this.$i18n.locale = 'ko' 
          break
        case 'id':
          this.$i18n.locale = 'id' 
          break
        case 'th':
          this.$i18n.locale = 'th' 
          break
        case 'zh-cn':
        case 'zh-tw':
          this.$i18n.locale = 'zh-cn' 
          break
        default :
          this.$i18n.locale = 'en'
      }
      this.simpleReady = true
      this.currency = data.gameDetail.currency_code
      /** 細單component */
      this.detailComponent = this.detailMode(data.gameDetail.game_class_id)
    },
    decodeBase64(encodeStr) {
      return atob(decodeURIComponent(encodeStr))
    },
    /* 平台查細單 */
    platformGetDetail(query) {
      this.apiUrl = `${location.protocol}//${this.decodeBase64(query.s)}`
      let api = `${this.apiUrl}/slot/betdetail_token`
      const language = query.language
      const token = query.token
      const params = {
        token,
      }
      axios.post(api,params)
        .then(({data})=>{
          this.gameCode = data.game_code
          this.detailFromBackstage = data
          this.roundID = data.spin_summary.round_code
          this.account = data.user_name
          switch(language) {
              case 'vi':
              this.$i18n.locale = 'vi' 
              break
              case 'ko':
              this.$i18n.locale = 'ko' 
              break
              case 'id':
              this.$i18n.locale = 'id' 
              break
            case 'th':
              this.$i18n.locale = 'th' 
              break
            case 'zh-cn':
              this.$i18n.locale = 'zh-cn' 
              break
            default :
              this.$i18n.locale = 'en'
          }
          this.currency = data.currency_code
          this.simpleReady = true
          /** 細單component */
          this.detailComponent = this.detailMode(data.game_class_id)
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    detailMode(gameClassID){
      switch(gameClassID){
        case 1:
          return this.customDetail.indexOf(this.gameCode) !== -1 ? `detail${this.gameCode}` : 'slotDetail'
          break;
        case 2:
          return 'fishDetail'
          break;
      }
    },
    changeCurrency(num){
      // 由子層傳入(遊戲細單)
      this.currency = num
    }
  },
  watch: {
    currPage(next, prev) {
      if (next !== prev) {
        this.searchType === 1 ? this.getListByDate() : this.getListByID()
      }
    },
  },
  created() {
    /** 後台查細單 */
    if (this.$route.name !== 'home') {
      if (!Object.keys(this.$route.query).length) {
        this.viewStatus = 'simple'
        window.addEventListener("message", this.postGetInfo, false)
      } else {
        this.viewStatus = 'simple'
        this.platformGetDetail(this.$route.query)
      }
    }else{
      if(this.$route.query.type === 'detail'){
        this.viewStatus = 'simple'
        window.addEventListener("message", (evt)=>{
          let gameCode = JSON.parse(evt.data).gameCode
          if(gameCode == 'BN65'){
            this.$router.push({ path: '/fishDetail' })
          }else{
            this.$router.push({ path: '/slotDetail' })
          }
          this.$nextTick(()=>{
            this.postGetInfo(evt)
          })
        }, false)
      }
    }
  },
  mounted() {
    const gameInfo = parent.Entry
    /** 開啟模式 => 遊戲查細單(full) / 後台查細單(simple) */
    if (this.viewStatus === 'full') {
      this.apiUrl = BACKEND_LOCATION
      
      if (gameInfo) {
        const apiUrl = `${document.location.protocol}//${gameInfo.getDetail.betQuery}`
        this.apiUrl = gameInfo.getDetail.betQuery ? apiUrl : BACKEND_LOCATION
      }
      this.gameInit()
    } else {
      parent.postMessage('childReady', '*')
    }
    const url = document.location.protocol === 'http:' ? `https://mstest.msgaming.one/gameList.json` : `${window.location.origin}/game/gameList.json`
    axios.get(url).then(({ data }) => {
      this.gameList = data.GameNames
    }).catch(error => {
      console.log(error)
    })
  },
  beforeDestroy() {
    window.removeEventListener("message", this.postGetInfo)
  }
}
</script>

<style lang="sass">
  @import '@css/index.sass'
  [span-CustomizedSearch="th"]
    margin-right: 2px
  [span-IDSearch]
  [span-IDSearch="vi"]
    margin-right: 22px
  [span-IDSearch="ko"]
    margin-right: 14px
  [span-IDSearch="id"]
    margin-right: 31px
</style>

