<template>
  <div id="detail" ref="detail" v-if="Object.keys(gameDetail).length" :class="{'simple': viewStatus !== 'full'}">
    <div v-if="viewStatus === 'full'" class="closeBtn" @click="closeDetail"></div>
    <perfect-scrollbar>
      <div class="container">
        <h1>{{ $t('DetailTitle')}}</h1>
        <h2>{{$t('Game')}} : {{ gameName }}</h2>
        <div class="bet-info">
          <div class="inner">
            <div class="header">
              <div class="number">{{ $t('RoundID') }}</div>
              <div class="user">{{ $t('UserAccount') }}</div>
              <div class="agent" v-if="false">{{ $t('AgentAccount') }}</div>
              <div class="proportion">{{ $t('Denom') }}</div>
              <div class="currency">{{ $t('Currency') }}</div>
            </div>
            <div class="content">
              <div class="number" :data-label="$t('RoundID')"><span>{{ roundId }}</span></div>
              <div class="user" :data-label="$t('UserAccount')"><span> {{ account }}</span></div>
              <!-- <div class="agent" v-if="false" :data-label="$t('AgentAccount')"><span> - </span></div> -->
              <div class="proportion" :data-label="$t('Denom')"><span>{{ denom }}</span></div>
              <div class="currency" :data-label="$t('Currency')"><span>{{ currency }}</span></div>
            </div>
          </div>
        </div>
        <h2>{{ $t('RoundDetail') }}</h2>
        <div class="bet-info result">
          <div class="inner">
            <div class="header">
              <div class="dateTime">{{ $t('time') }}</div>
              <div class="behavior">{{ $t('conduct') }}</div>
              <div class="amount">{{ $t('amount') }}</div>
            </div>
            <div class="content">
              <div class="dateTime">{{ dateTimeFormat(gameDetail.spin_summary.bet_time) }}</div>
              <div class="behavior">{{ $t('BetTotal') }}</div>
              <div class="amount">{{ amount }}</div>
            </div>
            <div class="content">
              <div class="dateTime">{{ dateTimeFormat(gameDetail.spin_summary.end_time) }}</div>
              <div class="behavior">{{ $t('WinTotal') }}</div>
              <div class="amount">{{ winAmount }}</div>
            </div>
          </div>
        </div>
        <div class="fish-detail" v-if="Object.keys(this.fishInfo.lists).length > 0">
          <h2>{{ $t('stage') }}：{{ $t(`level${fishInfo.level}`) }}</h2>
          <div class="bit-wrapper">
            <h3 class="bit-label">BET</h3>
            <template>
              <swiper class="bit-tab" :options="swiperOptions">
                <swiper-slide v-for="(row,key) in fishInfo.lists" :key="`fishBet-${key}`">
                  <a :class="{ 'active': Number(key) === fishInfo.currentBet }" @click="getGameScreenData(Number(key))">{{ Number(key) === 0 ? $t('all') : decimalNumber(key) }}</a>
                </swiper-slide>
              </swiper>
            </template>
            <a class="swiper-button-prev" slot="button-prev"></a>
            <a class="swiper-button-next" slot="button-next"></a>
          </div>
          <div class="bet-list">
            <div class="bet-info">
              <div class="inner-row">
                <div class="header">{{ $t('TotalBet') }}</div>
                <div class="content">{{ fishInfo.currentData.totalBet }}</div>
                <div class="header">{{ $t('TotalWin') }}</div>
                <div class="content">{{ fishInfo.currentData.totalWin }}</div>
              </div>
            </div>
            <table>
                <tr class="tb-head" v-if="Object.keys(fishInfo.currentData.fish).length > 0">
                  <th>{{ $t('fishType') }}</th>
                  <th>BET</th>
                  <th>{{ $t('kill') }}</th>
                  <th>{{ $t('WinTotal') }}</th>
                </tr>
                <tr v-for="(fish,key) in fishInfo.currentData.fish" :key="`fish-${key}`">
                  <td class="img">
                    <img :src="require(`@img/games/${gameCode}/fish-${key}.png`)" alt="">
                  </td>
                  <td>{{ fish.HitCount }}</td>
                  <td>{{ fish.Kill }}</td>
                  <td>{{ decimalNumber(fish.Win, 10) }}</td>
                </tr>
                <tr class="tb-head">
                  <th>{{ $t('skill') }}</th>
                  <th>{{ $t('get') }}</th>
                  <th>{{ $t('use') }}</th>
                  <th>{{ $t('WinTotal') }}</th>
                </tr>
                <tr class="tool" v-for="(skill,key) in fishInfo.currentData.skills" :key="`skill-${key}`">
                  <td class="img">
                    <img :src="require(`@img/games/${gameCode}/skill-${key}.png`)" alt="">
                  </td>
                  <td v-for="(val,idx) in skill" :key="`${skill}-${idx}`">{{ val }}</td>
                </tr>
            </table>
          </div>
        </div>
      </div>
    </perfect-scrollbar>
  </div>
</template>
<script>
import axios from 'axios'
import { Decimal } from 'decimal.js'
import moment from 'moment'
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import 'vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css'
import { Swiper, SwiperSlide, Pagination } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
import _ from 'lodash'
export default {
  name: 'fishDetail',
  props:{
    apiUrl: {
      type: String,
      default: '',
    },
    gameToken:{
      type: String,
      default: '',
    },
    gameCode:{
      type: String,
      default: '',
    },
    roundId: {
      type: String,
      default: '',
    },
    account: {
      type: String,
      default: '',
    },
    viewStatus: {
      type: String,
      default: '',
    },
    detailFromBackstage: {
      type: Object,
      default: () => {},
    },
    gameList: {
      type: Array,
      default: () => [],
    },
    currency: {
      type: [Number,String],
      default: '-',
    },
  },
  components: {
    PerfectScrollbar,
    Swiper,
    SwiperSlide,
  },
  data(){
    return{
      gameDetail: {},
      money_fraction_multiple: 0,
      jp_fraction_multiple: 0,
      gameName: '',
      swiperOptions: {
        slidesPerView: 4,
        // slidesPerGroup: 6,
        // loop: true,
        // loopFillGroupWithBlank: true,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      fishInfo:{
        lists: [],
        level: null,
        currentBet: 0,
        currentData: {},
      },
    }
  },
  computed: {
    bet() {
      return Number(Decimal.div(this.amount|| 0, this.multiple[this.gameCode]))
    },
    amount() {
      return Number(Decimal.div(this.gameDetail.spin_summary.bet || 0, this.gameDetail.money_fraction_multiple))
    },
    winAmount() {
      return Number(Decimal.div(this.gameDetail.spin_summary.win || 0, this.gameDetail.money_fraction_multiple))
    },
    denom() {
      return Number(Decimal.mul(this.gameDetail.spin_summary.denom || 0, 0.01))
    }
  },
  watch: {
    gameCode: {
      handler(val) {
        if (val) {
          this.getGameName()
        }
      },
      immediate: true,
    },
  },
  methods: {
    dateTimeFormat(dateTime) {
      return moment((dateTime)*1000).utcOffset(-240).format()
    },
    /** 取得遊戲名稱 */
    getGameName() {
      const gameId = Number(this.gameCode.replace('BN',''))
      let lang = this.$i18n.locale === 'tw' ? 'zh-tw' : this.$i18n.locale
      const gameNames = this.gameList.filter(v => {
        return v.ID === gameId
      })[0].Names
      const gameName = gameNames.filter(v=>{
        return v.lang === lang
      })[0].Name
      this.gameName = gameName
    },
    /** 查詢細單詳細資訊 */
    getDetail() {
      this.$emit("changeLoading", true);
      let api = `${this.apiUrl}/slot/betdetail`
      const params = {
        token : this.gameToken,
        round_code: this.roundId,
      }
      axios.post(api, params)
      .then(({ data }) => {
        this.money_fraction_multiple = data.money_fraction_multiple
        this.jp_fraction_multiple = data.jp_fraction_multiple
        this.$emit("changeCurrency", data.currency_code);
        // this.getGameName()
        this.gameDetail = data

      this.fishInfo.level = this.gameDetail.spin_details[0].FieldIndex
      this.fishInfo.lists = this.gameDetail.spin_details[0].FishInfos

        this.getGameScreenData(0)
      })
      .catch(function (error) {
        console.log(error);
      }).finally(() => {
        this.$emit("changeLoading", false);
      });
    },
    getResult() {
      const _self = this
      return {
      }
    },
    decimalNumber(num) {
      return Number(Decimal.div(Number(num), 10))
    },
    /** 整理盤面資料 */
    getGameScreenData(bet) {
      if(Object.keys(this.fishInfo.lists).length <= 0) return
      this.fishInfo.currentBet = bet
      let { FishInfo, GetSkill, SkillScore, UseSkill, TotalBet, TotalWin } = this.fishInfo.lists[String(bet)]
      let skillData = [GetSkill, UseSkill, SkillScore].flat()
      let skillkey = ['lock','freeze','laser']

      this.fishInfo.currentData = {
        id: bet,
        fish: FishInfo,
        skills: {},
        totalBet: this.decimalNumber(TotalBet, 10),
        totalWin: this.decimalNumber(TotalWin, 10)
      }

      skillkey.map((key,idx1)=>{
        this.fishInfo.currentData.skills[key] = []
        let ary = []
        skillData.map((data,idx2)=>{
          if(idx2%3==idx1) ary.push(data)
        })
        key=='freeze' || key=='lock' ? ary[2] = '-' : ary[2] = this.decimalNumber(ary[2], 10)
        this.fishInfo.currentData.skills[key] = ary
      })
    },
    /** 關閉細單視窗 */
    closeDetail() {
      this.$emit('detailPageSwitch')
    },
  },
  created() {},
  mounted() {
    /** 遊戲查細單 */
    if ( this.viewStatus === 'full' ) {
      this.getDetail()
    } else {
    /** 後台查細單 */
      this.money_fraction_multiple = this.detailFromBackstage.money_fraction_multiple
      this.jp_fraction_multiple = this.detailFromBackstage.jp_fraction_multiple
      // this.getGameName()
      this.gameDetail = this.detailFromBackstage
      this.fishInfo.level = this.gameDetail.spin_details[0].FieldIndex
      this.fishInfo.lists = this.gameDetail.spin_details[0].FishInfos
      this.$emit("changeLoading", false);
      this.getGameScreenData(0)

    }
  },
  beforeDestroy() {
    
  }
}
</script>
<style lang="sass">
  @import '@css/detail.sass'
  @import '@css/fish.sass'
</style>

