<template>
  <div id="lineScreen" :gameCode="gameCode" :style="{'width': width + 'px','height': height + 'px'}" v-if="winPosition.length || winLine || allScreen()">
    <template>
      <div v-for="(row, rindex) in screen" :key="`${winLine}_${rindex}`">
        <template v-for="(col, cindex) in row">
          <span :key="cindex" :class="{'enable': getLinePosition(rindex,cindex), 'active': getPosition(rindex,cindex)}"></span>
        </template>
      </div>
    </template>
  </div>
</template>
<script>
import LINE_RESULT from '@/constants/LineData'
export default {
  name: 'LineScreen',
  props:{
    gameCode: {
      type: String,
      default: '',
    },
    winLine: {
      default: 0,
    },
    winPosition: {
      type: Array,
      default: ()=>[],
    },
    screen: {
      type: Array,
      default: ()=>[],
    },
    gameType: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      px: 8,
      lineData: [],
      all: ['BN12','BN13','BN35','BN49'],
      straightLineGames: ['BN38'],
      straightLineArray: [],
    }
  },
  computed: {
    height() {
      let count = 0
      if(!this.screen.length) { return false}
      this.screen.map(v=>{
        count = v.length > count ? v.length : count
      })
      if (this.gameCode === 'BN39') {
        count = 1
      }
      return count * (this.px + 2)
    },
    width() {
      if(!this.screen.length) { return false}
      let count = this.screen.length
      if (this.gameCode === 'BN39') {
        count = 3
      }
      return count * (this.px + 2)
    },
  },
  mounted() {
    this.winScreen()
  },
  methods: {
    allScreen() {
      /** 全盤獎 */
      if(this.all.indexOf(this.gameCode) !== -1 && !this.winPosition.length) {
        return true
      } else {
        return false
      }
    },
    winScreen() {
      /** 不需取得線譜 */
      if(!this.winLine || isNaN(this.winLine || this.gameCode === 'BN39')) { return }
      /** 取得線譜 */
      const lineData = LINE_RESULT(this.gameCode)
      const index = Object.keys(lineData).indexOf(String(this.winLine))
        this.lineData = Object.keys(lineData).length && index !== -1 ? lineData[this.winLine] : []
      // else {
      //   this.lineData = Object.keys(lineData).length && index !== -1 ? lineData[this.winLine] : []
      // }
      if (this.lineData.length && this.straightLineGames.indexOf(this.gameCode) !== -1) {
        this.lineData.map(v=>{
          const str = v.reduce((prev, curr) => {
            return prev + String(curr)
          }, '')
          this.straightLineArray.push(str)
        })
      }
    },
    /** 實際中獎位置 */
    getPosition(rindex,cindex) {
      const position = this.winPosition
      if(this.all.includes(this.gameCode) && !position.length) {
        return true
      } else if ((this.gameCode === 'BN21' || this.gameCode === 'BN36' || this.gameCode === 'BN19') && position.length) {
        let active = false
        if (position[cindex][rindex]) {
            active = true
          }
        return active
      } else if (this.gameCode === 'BN39' && position.length) {
        return position[rindex][cindex] || false
      } else if(position.length) {
        let active = false
        position.map(v => {
          if (v[0] === rindex && v[1] === cindex) {
            active = true
          }
        });
        return active
      } else {
        return false
      }
    },
    /** 中獎線位置 */
    getLinePosition(rindex,cindex) {
      if (this.lineData.length && this.straightLineGames.indexOf(this.gameCode) !== -1) {
        return this.straightLineArray.indexOf(String(rindex)+String(cindex)) !== -1
      } else if (this.lineData.length) {
        return this.lineData[rindex][1] === cindex ? true : false
      } else {
        return false
      }
    },
  },
}
</script>
<style lang="sass">
  @import '@css/lineScreen.sass'
</style>