import Vue from 'vue'
import VueRouter from 'vue-router'

//page
import Home from '@/components/pages/Home/index';

Vue.use(VueRouter);

const router = new VueRouter({
  base: __dirname,
  routes: [
    { 
      path: '/', 
      name: 'home', 
      component: Home 
    },
    { 
      path: '/slotDetail', 
      name: 'slot', 
      component: Home
    },
    { 
      path: '/fishDetail',
      name: 'fish', 
      component: Home
    },
  ]
});

router.beforeEach((to, from, next) => {
  if (to.matched.length === 0) { 
    if(to.path === '/detail'){
      next({
        path: '/?type=detail'
      });
    }else{
      next('/'); 
    }
  } else {
    next();
  }
});
  
export default router;