<template>
  <component :is="comp" :gameCode="gameCode" :screen="screen" :outSideEnable="outSideEnable" />
</template>
<script>
import defaultComponent from './default/index'
export default {
  name: "ResultScreen",
  props: {
    gameCode: {
      type: String,
      default: ""
    },
    screen: {
      type: Array,
      default: () => []
    },
    outSideEnable: {
      type: Array,
      default: () => []
    },
  },
  components: {
    defaultComponent,
  },
  data() {
    return {
      comp: '',
      customList: [],
    }
  },
  mounted() {
    this.comp = this.customList.indexOf(this.gameCode) !== -1 ? this.gameCode : 'defaultComponent'
  }
};
</script>
<style lang="sass">
@import '@css/resultScreen.sass'
</style>