<template>
  <div id="detail" ref="detail" v-if="Object.keys(gameDetail).length" :class="{'simple': viewStatus !== 'full'}">
    <div v-if="viewStatus === 'full'" class="closeBtn" @click="closeDetail"></div>
    <perfect-scrollbar>
      <div class="container">
        <h1>{{ $t('DetailTitle')}}</h1>
        <h2>{{$t('Game')}} : {{ gameName }}</h2>
        <div class="bet-info">
          <div class="inner">
            <div class="header">
              <div class="number">{{ $t('RoundID') }}</div>
              <div class="user">{{ $t('UserAccount') }}</div>
              <div class="agent" v-if="false">{{ $t('AgentAccount') }}</div>
              <div class="proportion">{{ $t('Denom') }}</div>
              <div class="currency">{{ $t('Currency') }}</div>
            </div>
            <div class="content">
              <div class="number" :data-label=" $t('RoundID')"><span>{{ roundId }}</span></div>
              <div class="user" :data-label=" $t('UserAccount')"> <span>{{ account }}</span></div>
              <div class="proportion" :data-label=" $t('Denom')"><span>{{ denom }}</span></div>
              <div class="currency" :data-label=" $t('Currency')"><span>{{ currency }}</span></div>
            </div>
          </div>
        </div>
        <h2>{{ $t('RoundDetail') }}</h2>
        <div class="bet-info result">
          <div class="inner">
            <div class="header">
              <div class="dateTime">{{ $t('time') }}</div>
              <div class="behavior">{{ $t('conduct') }}</div>
              <div class="amount">{{ $t('amount') }}</div>
            </div>
            <div class="content">
              <div class="dateTime">{{ dateTimeFormat(gameDetail.spin_summary.bet_time) }}</div>
              <div class="behavior">{{ $t('BetTotal') }}</div>
              <div class="amount">{{ amount }}</div>
            </div>
            <div class="content">
              <div class="dateTime">{{ dateTimeFormat(gameDetail.spin_summary.end_time) }}</div>
              <div class="behavior">{{ $t('WinTotal') }}</div>
              <div class="amount">{{ winAmount }}</div>
            </div>
          </div>
        </div>
        <!-- normal game-->
        <h2>{{ $t('BaseGame') }}</h2>
        <div class="bet-detail" v-if="normalGames.length">
          <div>
            <section class="bet-result" :game-code="gameCode">
              <div class="gameDetail">
                <ul>
                  <li>{{ $t('BetLevel') }}: {{ normalGames[normalGamesIndex].bet }}</li>
                  <li>{{ $t('Payout') }}: {{ normalGames[normalGamesIndex].win }}</li>
                  <li>{{ $t('TotalWinLines') }}: {{ normalGames[normalGamesIndex].list.length }}</li>
                </ul>
              </div>
              <div class="gameScreen"><component :is="normalComponent" :game-code="gameCode" :normal-games="normalGames" @chgNormalGamesIndex="chgNormalGamesIndex"></component></div>
              <button v-if="normalGames[normalGamesIndex].list.length" class="detailSwitch" @click="normalGames[normalGamesIndex].detail = !normalGames[normalGamesIndex].detail" :class="{'enable': !normalGames[normalGamesIndex].detail}"><span></span><span></span></button>
            </section>
            <transition>
              <section class="bet-result-list" v-if="normalGames[normalGamesIndex].detail">
                <div class="result-list" v-for="(item, index) in normalGames[normalGamesIndex].list" :key="index">
                  <table>
                    <tr>
                      <th>{{ $t('LineImage') }}</th>
                      <th>{{ $t('LineCount') }}</th>
                      <th>{{ $t('Symbol') }}</th>
                      <th>{{ $t('Multiplier') }}</th>
                      <th>{{ $t('Payout') }}</th>
                    </tr>
                    <tr>
                      <td>
                        <div class="lineImage">
                          <span v-if="item.line_no">{{ item.line_no }}</span>
                          <template>
                            <lineScreen :key="`${normalGamesIndex}_${item.line_no}_${index}`" :game-code="gameCode" :screen="item.screen" :winLine="item.line_no" :winPosition="item.winPosition" />
                          </template>
                        </div>
                      </td>
                      <td>{{ item.symbol_count }}</td>
                      <td><img :src="require(`@img/games/${gameCode}/${item.symbol_id}.png`)" alt=""></td>
                      <td>{{ item.multiplier }}</td>
                      <td>{{ item.cash }}</td>
                    </tr>
                  </table>
                </div>
              </section>
            </transition>
          </div>
        </div>
        <!-- double game -->
        <template>
          <div class="bet-detail" v-for="(item, index) in doubleGames" :key="index">
            <div>
              <section class="bet-result">
                <div class="gameDetail doubleGame">
                  <ul>
                    <li>{{ $t('DoubleUpGame') }}: # {{index+1}}</li>
                    <li>{{ $t('Payout') }}: {{ item.win }}</li>
                  </ul>
                </div>
                <div class="gameScreen">
                  <div class="doubleScreen">
                    <table>
                      <tr>
                        <td>
                          <div><img :src="require(`@img/games/${gameCode}/${item.symbol_result[0]}.png`)" alt=""></div>
                          <span>{{ $t('dealer') }}</span>
                        </td>
                        <td></td>
                      </tr>
                      <tr>
                        <td>
                          <div><img :src="require(`@img/games/${gameCode}/${item.symbol_result[1]}.png`)" alt=""></div>
                          <span>{{ $t('player') }}</span>
                        </td>
                        <td class="not">
                          <div><span><img :src="require(`@img/games/${gameCode}/${item.symbol_result[2]}.png`)" alt=""></span><span><img :src="require(`@img/games/${gameCode}/${item.symbol_result[3]}.png`)" alt=""></span></div>
                          <span>{{ $t('NotSelected') }}</span>
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
                <button class="detailSwitch" @click="doubleGames[index].detail = !doubleGames[index].detail" :class="{'enable': !doubleGames[index].detail}"><span></span><span></span></button>
              </section>
              <transition>
                <template>
                  <section class="bet-result-list" v-if="doubleGames[index].detail">
                    <div class="result-list">
                      <table>
                        <tr>
                          <th>{{ $t('Symbol') }}</th>
                          <th>{{ $t('Payout') }}</th>
                        </tr>
                        <tr>
                          <td><img :src="require(`@img/games/${gameCode}/${item.symbol_result[1]}.png`)" alt=""></td>
                          <td>{{ item.win }}</td>
                        </tr>
                      </table>
                    </div>
                  </section>
                </template>
              </transition>
            </div>
          </div>
        </template>
        <!--jackpot-->
        <div class="bet-detail" v-if="jackpotGames.length">
          <div>
            <section class="bet-result">
              <div class="gameDetail doubleGame">
                <ul>
                  <li>{{ $t('JackpotGame') }}</li>
                  <li>{{ $t('Payout') }}: {{ jackpotGames[jackpotGamesIndex].cash }}</li>
                </ul>
              </div>
              <div class="gameScreen">
                <resultScreen :game-code="gameCode" :screen="jackpotGames[jackpotGamesIndex].symbol_result" />
              </div>
              <button v-if="jackpotGames.length" class="detailSwitch" @click="jackpotGames[jackpotGamesIndex].detail = !jackpotGames[jackpotGamesIndex].detail" :class="{'enable': !jackpotGames[jackpotGamesIndex].detail}"><span></span><span></span></button>
            </section>
            <transition>
              <section class="bet-result-list" v-if="jackpotGames[jackpotGamesIndex].detail">
                <div class="result-list">
                  <table>
                    <tr>
                      <th>{{ $t('Symbol') }}</th>
                      <th>{{ $t('Payout') }}</th>
                    </tr>
                    <tr>
                      <td><img :src="require(`@img/games/${gameCode}/J0${jackpotGames[jackpotGamesIndex].symbol_id}.png`)" alt=""></td>
                      <td>{{ jackpotGames[jackpotGamesIndex].cash }}</td>
                    </tr>
                  </table>
                </div>
              </section>
            </transition>
          </div>
        </div>
        <!-- free game-->
        <template v-if="freeGames.length">
          <div class="bet-detail" v-for="(free, index) in freeGames" :key="index">
            <div>
              <section class="bet-result">
                <div class="gameDetail freeGame">
                  <ul>
                    <li>{{ $t('FreeGame') }}: # {{index+1}}</li>
                    <li>{{ $t('Payout') }}: {{ free.detail[free.detailCur].win }}</li>
                    <li>{{ $t('TotalWinLines') }}: {{ free.detail[free.detailCur].list.length }}</li>
                  </ul>
                </div>
                <div class="gameScreen"><component :is="freeGameComponent" :game-code="gameCode" :free="free" :parentIndex="index" @chgFreeGameIndex="chgFreeGameIndex"></component></div>
                <button v-if="free.detail[free.detailCur].list.length" class="detailSwitch" @click="free.detail[free.detailCur].detail = !free.detail[free.detailCur].detail" :class="{'enable': !free.detail[free.detailCur].detail}"><span></span><span></span></button>
              </section>
              <transition>
                <component :is="freeTableComponent" :game-code="gameCode" :free="free" :index="index" v-if="free.detail"></component>
              </transition>
            </div>
          </div>
        </template>
        <!-- bonus games -->
        <template v-if="bounsGames.length">
          <div class="bet-detail" v-for="(bonus, index) in bounsGames" :key="`b_${index}`">
            <div>
              <section class="bet-result">
                <div class="gameDetail bonus">
                  <ul>
                    <li>{{ $t('BonusGame') }}: # {{index+1}}</li>
                    <li>{{ $t('Payout') }}: {{ bonus.win}}</li>
                  </ul>
                </div>
                <div class="gameScreen">
                  <resultScreen :game-code="gameCode" :screen="bonus.symbol_result" />
                </div>
                <button class="detailSwitch" @click="bonus.detail = !bonus.detail" :class="{'enable': !bonus.detail}"><span></span><span></span></button>
              </section>
              <transition>
                <component :is="bonusTableComponent" :game-code="gameCode" :bonus="bonus" v-if="bonus.detail"></component>
              </transition>
            </div>
          </div>
        </template>
      </div>
    </perfect-scrollbar>
  </div>
</template>
<script>
import axios from 'axios'
import { Decimal } from 'decimal.js'
import moment from 'moment'
import lineScreen from '@/components/pages/Home/lineScreen/index'
import resultScreen from '@/components/pages/Home/resultScreen/index'

import normalDefault from '@/components/pages/Home/slotDetail/screenFrom/normal/default/index'

import freeGameDefault from '@/components/pages/Home/slotDetail/screenFrom/freeGame/default/index'
// import { freeScreenBN82 } from '@/components/pages/Home/slotDetail/screenFrom/freeGame/custom'

import freeTableDefault from '@/components/pages/Home/slotDetail/tableFrom/free/default/index'

import bonusTableDefault from '@/components/pages/Home/slotDetail/tableFrom/bonus/default/index'

import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import 'vue2-perfect-scrollbar/dist/vue2-perfect-scrollbar.css'
import _ from 'lodash'
export default {
  name: 'newDetail',
  props:{
    apiUrl: {
      type: String,
      default: '',
    },
    gameToken:{
      type: String,
      default: '',
    },
    gameCode:{
      type: String,
      default: '',
    },
    roundId: {
      type: String,
      default: '',
    },
    account: {
      type: String,
      default: '',
    },
    viewStatus: {
      type: String,
      default: '',
    },
    detailFromBackstage: {
      type: Object,
      default: () => {},
    },
    gameList: {
      type: Array,
      default: () => [],
    },
    currency: {
      type: [Number,String],
      default: '-',
    },
  },
  components: {
    lineScreen,
    PerfectScrollbar,
    resultScreen,
    normalDefault,
    freeGameDefault,
    // freeScreenBN82,
    freeTableDefault,
    bonusTableDefault,
  },
  data(){
    return{
      gameDetail: {},
      multiple: {
        'BN82': 0.05,
      },
      money_fraction_multiple: 0,
      jp_fraction_multiple: 0,
      gameName: '',
      normalGames: [],
      normalGamesIndex: 0,
      normalGamesDetail:[],
      normalComponent: '',
      freeGames: [],
      freeGamesIndex: 0,
      freeGamesDetail:[],
      freeGameComponent: '',
      bounsGames: [],
      bounsGamesIndex: 0,
      bounsGamesDetail:[],
      bonusTableComponent: '',
      doubleGames:[],
      jackpotGames:[],
      jackpotGamesIndex: 0,
      customComponentList: { 
        // 'BN82': {
        //   'freeScreen': 'freeScreenBN82',
        // },
      },
    }
  },
  computed: {
    bet() {
      return Number(Decimal.div(this.amount|| 0, this.multiple[this.gameCode]))
    },
    amount() {
      return Number(Decimal.div(this.gameDetail.spin_summary.bet || 0, this.gameDetail.money_fraction_multiple))
    },
    winAmount() {
      return Number(Decimal.div(this.gameDetail.spin_summary.win || 0, this.gameDetail.money_fraction_multiple))
    },
    denom() {
      return Number(Decimal.mul(this.gameDetail.spin_summary.denom || 0, 0.01))
    }
  },
  watch: {
    gameCode: {
      handler(val) {
        if (val) {
          this.getGameName()
        }
      },
      immediate: true,
    },
  },
  methods: {
    dateTimeFormat(dateTime) {
      return moment((dateTime)*1000).utcOffset(-240).format()
    },
    /** 取得遊戲名稱 */
    getGameName() {
      const gameId = Number(this.gameCode.replace('BN',''))
      let lang = this.$i18n.locale === 'tw' ? 'zh-tw' : this.$i18n.locale
      const gameNames = this.gameList.filter(v => {
        return v.ID === gameId
      })[0].Names
      const gameName = gameNames.filter(v=>{
        return v.lang === lang
      })[0].Name
      this.gameName = gameName
    },
    /** 查詢細單詳細資訊 */
    getDetail() {
      this.$emit("changeLoading", true);
      let api = `${this.apiUrl}/slot/betdetail`
      const params = {
        token : this.gameToken,
        round_code: this.roundId,
      }
      axios.post(api, params)
      .then(({ data }) => {
        this.money_fraction_multiple = data.money_fraction_multiple
        this.jp_fraction_multiple = data.jp_fraction_multiple
        this.$emit("changeCurrency", data.currency_code);
        // this.getGameName()
        this.gameDetail = data
        try {
          this.getResult()[this.gameCode]()
        } catch {
          this.getGameScreenData()
        }
      })
      .catch(function (error) {
        console.log(error);
      }).finally(() => {
        this.$emit("changeLoading", false);
      }); 
    },
    getResult() {
      const _self = this
      return {
        BN82() {
          const { spin_details } = _self.gameDetail
          let freeList = []
          spin_details.map(v =>{
            const data = {}
            const { symbol_result, win_line_infos, multiplier, screen_org, screen_output } = v.info

            /** free game */
            if (Number(v.game_state_type) === 1) {
              const detail = {}
              detail.screen_org = screen_org
              detail.screen_output = screen_output
              detail.symbol_result = symbol_result
              detail.win = Number(Decimal.div(v.win, _self.money_fraction_multiple))
              detail.list = []
              detail.detail = false

              /** 有中獎 */
              if (win_line_infos && win_line_infos.length) {
                win_line_infos.map(w=>{
                  const list = {}
                  if(String(w.line_no) !== '0') {
                    list.line_no = w.line_no
                  } else if (w.win_type & 0x02) {
                    list.line_no = 'F'
                  }
                  list.screen = symbol_result
                  list.winPosition = w.win_position
                  list.symbol_count = w.symbol_count
                  list.symbol_id = w.symbol_id
                  list.multiplier = multiplier
                  list.cash = Number(Decimal.mul(Decimal.div(w.win, _self.money_fraction_multiple), multiplier))
                  detail.list.push(list)
                  detail.detail = true
                })
              }
              freeList.push(detail)
              data.detailCount = freeList.length
              data.detailCur = 0
              data.detail = _.cloneDeep(freeList)
              _self.freeGames.push(data)
              freeList.length = 0
            } else {
              /** normal game */
              data.screen_org = screen_org
              data.symbol_result = symbol_result
              data.bet = _self.bet
              data.win = Number(Decimal.div(v.win, _self.money_fraction_multiple))
              data.list = []
              data.detail = false
              if (win_line_infos && win_line_infos.length) {
                win_line_infos.map(w=>{
                  const list = {}
                  if(String(w.line_no) !== '0') {
                    list.line_no = w.line_no
                  } else if (w.win_type & 0x02) {
                    list.line_no = 'F'
                  }
                  list.screen = symbol_result
                  list.symbol_count = w.symbol_count
                  list.symbol_id = w.symbol_id
                  list.multiplier = multiplier
                  list.cash = Number(Decimal.div(w.win, _self.money_fraction_multiple))
                  list.winPosition = w.win_position
                  data.list.push(list)
                  data.detail = true
                })
              }

              if(String(v.game_state_type) === '0') {
                _self.normalGames.push(data)
              }
            }
          })
        },
      }
    },
    /** 整理盤面資料 */
    getGameScreenData() {
      const { spin_details } = this.gameDetail
      let bonus_screen = []
      let freeList = []
      spin_details.map(v =>{
        const data = {}
        const { symbol_result, win_line_infos, multiplier, screen_org, options } = v.info

        /** free game */
        if (Number(v.game_state_type) === 1) {
          const detail = {}
          detail.screen_org = screen_org
          detail.symbol_result = symbol_result
          detail.win = Number(Decimal.div(v.win, this.money_fraction_multiple))
          detail.list = []
          detail.detail = false

          /** 有中獎 */
          if (win_line_infos && win_line_infos.length) {
            win_line_infos.map(w=>{
              const list = {}
              if(String(w.line_no) !== '0') {
                list.line_no = w.line_no
              } else if (w.win_type & 0x02) {
                list.line_no = 'F'
              }
              list.screen = symbol_result
              list.winPosition = w.win_position
              list.symbol_count = w.symbol_count
              list.symbol_id = w.symbol_id
              list.multiplier = multiplier
              list.cash = Number(Decimal.mul(Decimal.div(w.win, this.money_fraction_multiple), multiplier))
              detail.list.push(list)
              detail.detail = true
            })
          }
          freeList.push(detail)
          data.detailCount = freeList.length
          data.detailCur = 0
          data.detail = _.cloneDeep(freeList)
          this.freeGames.push(data)
          freeList.length = 0
        } else {
          data.screen_org = screen_org
          data.symbol_result = symbol_result
          data.bet = this.bet
          data.win = Number(Decimal.div(v.win, this.money_fraction_multiple))
          data.list = []
          data.detail = false
          if (win_line_infos && win_line_infos.length) {
            win_line_infos.map(w=>{
              const list = {}
              if(String(w.line_no) !== '0') {
                list.line_no = w.line_no
              } else if (w.win_type & 0x02) {
                list.line_no = 'F'
              } else if (w.win_type & 0x04) {
                list.line_no = 'B'
                bonus_screen = symbol_result
              } 
              list.screen = symbol_result
              list.symbol_count = w.symbol_count
              list.symbol_id = w.symbol_id
              list.multiplier = multiplier
              list.cash = Number(Decimal.div(w.win, this.money_fraction_multiple))
              list.winPosition = w.win_position
              data.list.push(list)
              data.detail = true
            })
          } else if ( v.game_state_type === 2 && options && options.length) { /** bouns game */
            data.screen_org = bonus_screen
            data.symbol_result = bonus_screen
            options.map(w=>{
              const list = {}
              list.selected_value = w.selected_value
              list.cash = Number(Decimal.div(Decimal.mul(w.selected_value, this.gameDetail.spin_summary.bet), this.money_fraction_multiple))
              list.symbol_id = Object.keys(w).indexOf('symbol_id') !== -1 ? w.symbol_id : false
              data.list.push(list)
              data.detail = true
            })
            this.bounsGames.push(data)
          }

          if(String(v.game_state_type) === '0') {
            this.normalGames.push(data)
          }
        }
      })
    },
    /** 關閉細單視窗 */
    closeDetail() {
      this.$emit('detailPageSwitch')
    },
    /** change normalGamesIndex */
    chgNormalGamesIndex(index) {
      this.normalGamesIndex = index
    },
    /** 判斷各盤面是否客製化 */
    customComponent() {
      const cusGame = Object.keys(this.customComponentList).indexOf(this.gameCode) !== -1 ? true : false
      this.normalComponent = 'normalDefault'
      this.freeGameComponent = 'freeGameDefault'
      this.freeTableComponent = 'freeTableDefault'
      this.bonusTableComponent = 'bonusTableDefault'
      if (cusGame) {
        const cusType = Object.keys(this.customComponentList[this.gameCode])
        cusType.map(v => {
          /** 判斷是否客製化 normalComponent */
          if (v === 'normalScreen') { this.normalComponent = this.customComponentList[this.gameCode].normalScreen }
          
          /** 判斷是否客製化 freeComponent */
          if (v === 'freeScreen') { this.freeGameComponent = this.customComponentList[this.gameCode].freeScreen }

          /** 判斷是否客製化 freeTableComponent */
          if (v === 'freeTable') { this.freeTableComponent = this.customComponentList[this.gameCode].freeTable }
          
          /** 判斷是否客製化 bonusTableComponent */
          if (v === 'bonusTable') { this.bonusTableComponent = this.customComponentList[this.gameCode].bonusTable }
        })
      }
    },
    /** Free game index change */
    chgFreeGameIndex(parentIndex, freeGamesIndex) {
      this.freeGames[parentIndex].detailCur = freeGamesIndex
    },
  },
  created() {},
  mounted() {
    this.customComponent()
    /** 遊戲查細單 */
    if ( this.viewStatus === 'full' ) {
      this.getDetail()
    } else {
    /** 後台查細單 */
      this.money_fraction_multiple = this.detailFromBackstage.money_fraction_multiple
      this.jp_fraction_multiple = this.detailFromBackstage.jp_fraction_multiple
      // this.getGameName()
      this.gameDetail = this.detailFromBackstage
      this.$emit("changeLoading", false);
      try {
        this.getResult()[this.gameCode]()
      } catch {
        this.getGameScreenData()
      }
    }
  },
  beforeDestroy() {
    
  }
}
</script>
<style lang="sass">
  @import '@css/detail.sass'
</style>

