<template>
  <div>
    <resultScreen :game-code="gameCode" :screen="normalGames[normalGamesIndex].symbol_result" :outSideEnable="normalGames[normalGamesIndex].stageEnable" />
    <div class="pager" v-if="normalGames.length > 1">
      <ul>
        <li class="prevPage" @click="chgPage(normalGames.length,-1)"><img src="data:image/svg+xml;base64," alt=""></li>
        <li><select v-model="normalGamesIndex"><option v-for="(item, index) in normalGames" :key="index" :value="index">{{ index+1 }}</option></select></li>
        <li>/</li>
        <li>{{ normalGames.length }}</li>
        <li class="nextPage" @click="chgPage(normalGames.length,1)"><img src="data:image/svg+xml;base64," alt=""></li>
      </ul>
    </div>
  </div>
</template>
<script>
import resultScreen from '@/components/pages/Home/resultScreen/index'
export default {
  name: 'Default',
  components: {
    resultScreen,
  },
  props: {
    normalGames: {
      type: Array,
      default: () => [],
    },
    gameCode:{
      type: String,
      default: '',
    },
  },
  watch: {
    normalGamesIndex(next) {
      this.$emit('chgNormalGamesIndex', next)
    },
  },
  data() {
    return {
      normalGamesIndex: 0,
    }
  },
  methods: {
    chgPage(pageCount, val) {
      if(this.normalGamesIndex + val < 0) {
        this.normalGamesIndex = pageCount - 1
      } else if (this.normalGamesIndex + val === pageCount) {
        this.normalGamesIndex = 0
      } else {
        this.normalGamesIndex +=  val
      }
      this.$emit('chgNormalGamesIndex', this.normalGamesIndex)
    },
  },
}
</script>