const getProtocolStr = () =>{
  const ProtocolStr = document.location.protocol;
  return ProtocolStr
}

const getEndpoint = () => {
  if (process.env.VUE_APP_ENV === 'development') {
    return process.env.VUE_APP_BASE_API
  }
  return `${getProtocolStr()}//${window.location.hostname}`
};

